import { VuexModule, Module, Mutation, Action, getModule } from 'vuex-module-decorators'
import store from '@/store'
import { ChatRecordDO, MessageType } from '@/database/dos/ChatRecord'
import { UserModule } from '../user'

export interface ChatRecordCellVO {
  isMe: boolean
  photo: string,
  type: MessageType,
  content: string
}

export interface IChatState {
  img: string
  name: string
  currentConversion: number
  currentRecords: ChatRecordCellVO[]
}

@Module({ dynamic: true, store, name: 'chat' })
class Chat extends VuexModule implements IChatState {
  public currentConversion = 0
  public img = "头像"
  public name = "名称"
  public chats = require("@/data/chats.json")
  public currentRecords: ChatRecordCellVO[] = []

  @Mutation
  private SET_HEAD(head: { name: string, img: string | null, id: number | null }) {
    console.log("set name:" + head.name)
    this.name = head.name
    if (head.img != null) {
      this.img = head.img
    }
    if (head.id != null) {
      this.currentConversion = head.id
    }

  }

  @Mutation
  private SET_CHAT_RECORD(list: ChatRecordCellVO[]) {
    this.currentRecords = list
  }

  @Mutation
  private ADD_CHAT_RECORD(cr: ChatRecordCellVO) {
    this.currentRecords.push(cr)
    this.currentRecords = this.currentRecords.slice()
  }

  @Action
  public setHead(head: { name: string, img: string | null, id: number | null }) {
    this.SET_HEAD(head)
    if(head.id == null){
      return
    }
    
    let data = this.chats[head.id]

    let init:string = data.init
    let img = data.img
    let ds = init.split("|")
    let list: ChatRecordCellVO[] = [];
    ds.forEach((val, index, array) => {
      if(index == 0 || val == "") {
        return
      }
      let p: ChatRecordCellVO = {
        isMe: "用户" == val.substring(0,2),
        photo: img,
        type: MessageType.text,
        content: val.split("：")[1]
      }
      list.push(p)
    })
    
    this.SET_CHAT_RECORD(list)
    
  }

  @Action
  public addChatRecord(cr: ChatRecordCellVO) {
    this.ADD_CHAT_RECORD(cr)
  }



}

export const ChatModule = getModule(Chat)
